import {useState} from 'react';
import mockDataBranches from "../../data/mockDataBranches.json"

function BranchState(props) {
    //if props are null, set state to default data
    //console.log("these are the BranchState hook props:")
    //console.log(JSON.stringify(props));
    //TODO mockDataBranches -> useApi()
    const [branches, setBranches] = useState(
        props && props.id
            ? mockDataBranches.branches.filter(obj => obj.id === props.id)
            : mockDataBranches.branches.map(obj => obj)
    );

    return [branches, setBranches]; // return the state and the function as an array

}

export default BranchState;

