import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Logo from '../assets/logo.png'
import {Image} from "react-bootstrap";
function NavigationBar() {
    return (
        <Navbar bg="light" expand="lg">
            <Container>
                <Navbar.Brand href="#home">
                    <Image  id="logo" src={Logo} width={150} height={75} alt="Canon Technologies"/>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link href="/reportLibrary">Report Library</Nav.Link>
                        <Nav.Link href="/testdetails">Test Details DEBUG ONLY</Nav.Link>
                        <Nav.Link href="/dashboard">CI Dashboard</Nav.Link>
                        <Nav.Link href="/heatmap">Heatmap</Nav.Link>
                        <Nav.Link href="/login">Login</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}
export default NavigationBar;