import {Badge, ListGroup} from "react-bootstrap";
import Container from "react-bootstrap/Container";
import {Fragment} from "react";
import {Link} from "react-router-dom";

function BranchSelectorItem(props) {

    let failCount = 0;
    let passCount = 0;
    const handleSelection = () => {
        //  console.log("SELECTED BRANCH WITH ID N." + props.branch.id);
        props.setSelectedBranch(props.branch);
        props.setSelectedTestSet(null);
    }
    props.branch.test_sets.forEach((testSet) => {
        testSet.tests.forEach((test) => {
            if (test.result === 0) {
                failCount++;
            } else if (test.result === 1) {
                passCount++;
            }
        });
    });

    return (
        <Container>
            <Fragment key={props.branch.id}>
                <ListGroup.Item
                    as="li"
                    className="d-flex justify-content-between align-items-start">
                    <div className="ms-2 me-auto">
                        <div className="fw-bold">
                            {/* TOLTO DAL LINK QUI SOTTO QUESTA PARTE DI CODICE: to={`/ReportLibrary/${object.id}`} */}
                            {/* ED AGGIUNTO L'onClick - A.M. 4 aprile 2023 */}
                            {/*TODO added BranchState(props). must be changed to remove handleselection*/}
                            <Link onClick={() => {
                                handleSelection();

                            }}>{props.branch.name}</Link>
                        </div>
                        <Badge bg="secondary">
                            Passed: {passCount}{"\n"}Failed: {failCount}
                        </Badge><br/>
                        <Badge bg="primary">
                            Commit ID: {props.branch.last_commit}
                        </Badge>
                    </div>
                </ListGroup.Item>
                {/* index < Branches.branches.length - 1 && <br/> */}
                {/* add a <br> element except for the last item */}
            </Fragment>
        </Container>
    );
}

export default BranchSelectorItem;