import Container from "react-bootstrap/Container";

function Path(props) {
    
    let path = "";


    const checkPath = () => {
        if (props.selectedBranch != null) {
            path += props.selectedBranch.name
        }

        if (props.selectedTestSet != null) {
            path += " > " + props.selectedTestSet.name
        }

        if (props.selectedTest != null) {
            path += " > " + props.selectedTest.name
        }
    }

    return (
        <Container>
            <div style={{color: "red"}}><b>Viewing Path:</b> {checkPath()}{path}</div>
            <br></br>
        </Container>

    );
}

export default Path;