import Chart from 'chart.js/auto';
import {useEffect, useRef, useState} from "react";
import "./ReportLibraryStatistics.css";
import Container from "react-bootstrap/Container";
import BranchesTestResult from "../hooks/data/BranchesTestResult";

function ReportLibraryStatistics(props) {
    const [branches, setBranches] = useState(props.branches);
    //TODO decouple chart code in another component & hook
    const pieChartRef = useRef(null);
    const lineChartRef = useRef(null);

    const results = [];

    if (props.branches && Object.keys(props.branches).length > 0) {
        results.push(BranchesTestResult(props.branches));
    } else {
        results.push(BranchesTestResult());
    }
    useEffect(() => {
        const pieChart = new Chart(pieChartRef.current, {
            type: 'pie',
            data: {
                labels: ['Pass', 'Fail', 'Other'],
                datasets: [
                    {
                        label: 'Test Results',
                        data: [0, 0, 0],
                        backgroundColor: ['green', 'red', 'blue'],
                    },
                ],
            },
        });
        const data = results[0][0].map((result) => result.passedTests + result.failedTests + result.otherTests);
        pieChart.data.datasets[0].data = data;
        pieChart.update();
        return () => {
            pieChart.destroy();
        };
    }, [props]);
    useEffect(() => {
        const ctx = lineChartRef.current.getContext("2d");
        const chartData = {
            labels: ['Pass', 'Fail', 'Other'],
            datasets: [
                {
                    label: "Passed Tests",
                    data: results[0][0].map(result => result.passedTests),
                    fill: false,
                    borderColor: "green"
                },
                {
                    label: "Failed Tests",
                    data: results[0][0].map(result => result.failedTests),
                    fill: false,
                    borderColor: "red"
                },
                {
                    label: "Other Tests",
                    data: results[0][0].map(result => result.otherTests),
                    fill: false,
                    borderColor: "yellow"
                }
            ]
        };
        const options = {
            scales: {
                xAxes: [{
                    type: "time",
                    time: {
                        unit: "day"
                    },
                    distribution: "linear",
                }]
            }
        };
        const lineChart = new Chart(ctx, {
            type: 'line',
            data: chartData,
            options: options
        });
        return () => {
            lineChart.destroy();
        };
    }, [props]);
    return (
        <Container className="square border border-1">
            <div style={{display: "flex", justifyContent: "center"}}>
                <div style={{width: "150px", height: "150px", marginRight: "10px"}}>
                    <canvas ref={pieChartRef}/>
                </div>
                <div>
                    Passed Tests: {results[0][0].map((result) => result.passedTests.valueOf().toFixed())}
                    <br></br>
                    Failed Tests: {results[0][0].map((result) => result.failedTests.valueOf())}
                    <br></br>
                    Other Tests: {results[0][0].map((result) => result.otherTests.valueOf())}
                </div>
                <div style={{width: "450px", height: "150px"}}>
                    <canvas ref={lineChartRef}/>
                </div>
            </div>
        </Container>
    );
}

export default ReportLibraryStatistics;