import {Col, ListGroup} from "react-bootstrap";
import Container from "react-bootstrap/Container";
import BranchSelectorItem from "./BranchSelectorItem";

function BranchSelector(props) {

    const branchSelectorItems = props.branches.map((branch) =>
        <BranchSelectorItem key={branch.id}
                            branch={branch}
                            setSelectedBranch={props.setSelectedBranch}
                            setSelectedTestSet={props.setSelectedTestSet}

        />
    )
    return (
        <Container>
            <Col className="d-flex justify-content-left border"
                 style={{display: "inline-block", width: "fit-content"}}
            >
                <ListGroup as="ol" numbered>
                    {branchSelectorItems}
                </ListGroup>
            </Col>
        </Container>
    );
}

export default BranchSelector;