import Container from "react-bootstrap/Container";
import Table from 'react-bootstrap/Table';
import Test from "./Test";


function TestsList(props) {

    const testItems = props.selectedTestSet.tests.map((test) => 
        <Test test={test} setSelectedTest={props.setSelectedTest}/>
    )

    return (
        <Container>
            <h1>Tests for TestSet: "{props.selectedTestSet.name}":</h1>
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>Test ID</th>
                        <th>Name</th>
                    </tr>
                </thead>
                <tbody>
                    {testItems}
                </tbody>
            </Table>
        </Container>

    );
}

export default TestsList;