import Container from "react-bootstrap/Container";
import Table from 'react-bootstrap/Table';
import DisplayResult from "./DisplayResult";
import React, {useState} from "react";

function TestDetails(props) {
    const [selectedTestObject, setSelectedTestObject] = useState(props);
    const selectedTest = selectedTestObject.selectedTest;
    const subTests = selectedTest.subtests.map(subtest => (
        <>
            <th>Sub Tests</th>
            <tr>
                <td>{subtest.id}</td>
                <td>{subtest.name}</td>
                <td><DisplayResult result={subtest.result}/></td>
                <td>{subtest.details}</td>
            </tr>
            <th>Steps</th>
            {subtest.testSteps.map(step => {
                let resultType;
                switch (step.result_type_id) {
                    case "0":
                        resultType = "false";
                        break;
                    case "1":
                        resultType = "pass";
                        break;
                    case "2":
                        resultType = "crash";
                        break;
                    default:
                        resultType = '';
                }
                console.log(resultType);

                    return (
                        <tr>
                            <td>{step.id}</td>
                            <td>{step.stepNumber}</td>
                            <td>{resultType}</td>
                            <td>{step.result_details}</td>
                        </tr>
                    );

            })}
        </>
    ));



    return (
        <Container>
            <Table striped bordered hover>
                <thead>
                <tr>
                    <th>ID</th>
                    <th>Name</th>
                    <th>Result</th>
                    <th>Details</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                    <td>{selectedTest.id}</td>
                    <td>{selectedTest.name}</td>
                    <td><DisplayResult result={selectedTest.result}/></td>
                    <td>SEE BELOW</td>
                </tr>
                {subTests}
                </tbody>
            </Table>

        </Container>

    );
}

export default TestDetails;