import React, {useEffect, useState} from "react";
import BranchSelector from "../components/BranchSelector";
import Path from "../components/Path";
import Container from "react-bootstrap/Container";
import TestSetsList from "../components/TestSetsList";
import TestsList from "../components/TestsList";
import TestDetails from "../components/TestDetails";
import ReportLibraryStatistics from "../components/ReportLibraryStatistics";
import BranchState from "../hooks/data/BranchState";
import BranchesTestResults from "../hooks/data/BranchesTestResult";


function ReportLibraryPage(props) {

    document.title = 'Report Library - CPP Testing';

    const [selectedBranch, setSelectedBranch] = useState(null);
    // this state will be passed to as props and changed by the BranchSelectorItem Component

    const [selectedTestSet, setSelectedTestSet] = useState(null);
    // this state will be passed to as props and changed by the TestSetsList Component

    const [selectedTest, setSelectedTest] = useState(null);

    // This useEffect works as a BackUp: it only works if selectedBranch state
    // changes to a different branch. So if the user selected branch1 and testset1
    // and then selects branch1 again, the screen would not update to show testsets again
    // Fixed this by setting the selectedTestSet to null when the branch link is pressed again
    // in the BranchSelectorItem component. Having both solutions causes this component to render
    // many times. May be removed in the future to improve performance.
    useEffect(() => {
        setSelectedTestSet(null);
        setSelectedTest(null);
    }, [selectedBranch]);

    // This function renders different components based on the user selections
    const renderBasedOnSelection = () => {
        if (selectedTestSet == null) {
            return (
                <TestSetsList selectedBranch={selectedBranch} selectedTestSet={selectedTestSet}
                              setSelectedTestSet={setSelectedTestSet}/>
            );
        } else if (selectedTestSet != null && selectedTest == null) {
            return (
                <TestsList selectedTestSet={selectedTestSet} setSelectedTest={setSelectedTest}/>
            );
        } else if (selectedTest != null) {
            return (
                <>
                    <TestDetails selectedTest={selectedTest}/>
                </>
            );
        }
    }
    const [branches, setBranches] = BranchState();
    BranchesTestResults(selectedBranch);
    return (
        <Container>
            <ReportLibraryStatistics branches={selectedBranch}/>
            <br></br>
            <div class="row">
                <div class="col-12"><Path selectedBranch={selectedBranch} selectedTestSet={selectedTestSet}
                                          selectedTest={selectedTest}/></div>
                <div class="col-2"><BranchSelector setSelectedBranch={setSelectedBranch} branches={props.branches}
                                                   setSelectedTestSet={setSelectedTestSet}/></div>
                <div class="col-10">{renderBasedOnSelection()}</div>
            </div>
        </Container>
    )
}

export default ReportLibraryPage;