import React, {useState} from "react";
import 'bootstrap/dist/css/bootstrap.css';
import {BrowserRouter, Route, Routes} from 'react-router-dom';
import HomePage from "./pages/HomePage";
import ReportLibraryPage from "./pages/ReportLibraryPage";
import NavigationBar from "./components/NavigationBar";
import BranchState from "./hooks/data/BranchState"

function App() {
    document.title = 'CPP Testing';

    const [branches, setBranches] = BranchState();
    const handleBranchesChange = (newBranches) => {
        setBranches(newBranches);
    };
    return (
        <BrowserRouter>
            <NavigationBar/>
            <Routes>cmd
                <Route path={"/"}
                       element={<HomePage/>}/>
                <Route path="/ReportLibrary"
                       element={<><ReportLibraryPage
                           branches={branches}/></>}/>
                <Route path="/api/branches/:branchId/test-sets/:testSetId/tests/:testId/subtests"
                       element={<><ReportLibraryPage/></>}/>
                <Route path="/api/branches/:branchId/test-sets/:testSetId/tests"
                       element={<><ReportLibraryPage/></>}/>
                <Route path="/api/branches/:branchId/test-sets"
                       element={<><ReportLibraryPage/></>}/>
                <Route path="/api/branches"
                       element={<><ReportLibraryPage/></>}/>
            </Routes>
        </BrowserRouter>
    );
}

export default App;

