import {Link} from "react-router-dom";

function TestsList(props) {

    const handleSelection = () => {
        props.setSelectedTest(props.test);
    }

    return (
        <tr>
            <td>{props.test.id}</td>
            <td><Link onClick={handleSelection}>{props.test.name}</Link></td>
        </tr>

    );
}

export default TestsList;