import Container from "react-bootstrap/Container";
import Table from 'react-bootstrap/Table';
import TestSet from "./TestSet"

function TestSetsList(props) {

    if (props.selectedBranch == null)
    {
        return(
            <div>Select a branch to start!</div>
        )
    }
    else if (props.selectedTestSet == null) {
        const testSetItems = props.selectedBranch.test_sets.map((test_set) => 
            <TestSet key={test_set.id} selectedTest={props.selectedTest} test_set={test_set} setSelectedTestSet={props.setSelectedTestSet}/>
        )

        return (
            <Container>
                <h1>Test Sets for Branch "{props.selectedBranch.name}":</h1>
                <Table striped bordered hover>
                    <thead>
                        <tr>
                            <th>Test Set ID</th>
                            <th>Name</th>
                            <th>Number of Tests</th>
                        </tr>
                    </thead>
                    <tbody>
                        {testSetItems}
                    </tbody>
                </Table>
            </Container>

        );
    }
}

export default TestSetsList;