import {Link} from "react-router-dom";

function TestSet(props) {

    const handleSelection = () => {
        props.setSelectedTestSet(props.test_set);
    }

    return (
        
        <tr>
            <td>{props.test_set.id}</td>
            <td><Link onClick={handleSelection} >{props.test_set.name}</Link></td>
            <td>{props.test_set.tests.length}</td>
        </tr>
    )
    
}

export default TestSet;